import React from "react";
import { useHistory } from "react-router-dom";
import "./Project.css";

import yarn1 from "../img/yarn1.png";
import yarn2 from "../img/yarn2.png";
import yarn3 from "../img/yarn3.png";
import virus1 from "../img/mockup1.png";
import virus2 from "../img/mockup2.png";
import findfun from "../img/findfun.png";
import green1 from "../img/green1.png";
import green2 from "../img/green2.png";
import mt1 from "../img/mock_mt_1.png";
import mt2 from "../img/mock_mt_2.png";
import mt3 from "../img/mock_mt_3.png";
import mailto from "../img/mailto.png";
import linkedin from "../img/linkedin.png";
import Fade from "react-reveal/Fade";

const Project = () => {
  const history = useHistory();

  window.onscroll = function () {
    scrollFunction();
  };

  function linkClick() {
    let width = window.innerWidth;
    var li = document.getElementById("link");
    var lis = document.getElementById("links");
    if (width < 769) {
      li.style.width = "100%";

      lis.style.display = "block";
    } else {
      if (document.getElementById("link") !== "null") {
        console.log(li.style.width);
        if (li.style.width === "90%") {
          li.style.width = "0.3vw";
          li.style.height = "1vw";
          lis.style.display = "none";
        } else if (li.style.width === "0.3vw") {
          li.style.width = "90%";
          lis.style.display = "block";
        }
      }
    }
  }

  function scrollFunction() {
    let width = window.innerWidth;
    var li = document.getElementById("link");
    var lis = document.getElementById("links");
    if (width < 769) {
      li.style.width = "100%";

      lis.style.display = "block";
    } else {
      if (
        document.body.scrollTop > 50 ||
        document.documentElement.scrollTop > 50
      ) {
        li.style.width = "0.3vw";

        li.style.height = "1vw";
        lis.style.display = "none";
        li.onclick = linkClick;
      } else {
        li.style.width = "90%";
        lis.style.display = "block";
        li.onclick = linkClick;
      }
    }
  }
  function handleClick() {
    history.push("/aliceljungholm");
  }
  function handleClickMasterThesis() {
    history.push("/aliceljungholm/master-thesis");
  }
  function handleClickAbout() {
    history.push("/aliceljungholm/about");
  }

  function handleHomeClick() {
    history.push("/aliceljungholm");
  }

  function handleYarn() {
    history.push("/aliceljungholm/Project/Yarn");
  }
  function handleCombat() {
    history.push("/aliceljungholm/Project/Combat");
  }
  function handleFind() {
    history.push("/aliceljungholm/Project/FindFun");
  }
  function handleGreen() {
    history.push("/aliceljungholm/Project/GreenLife");
  }

  return (
    <div className="pro">
      <div id="link">
        <Fade left>
          <div id="links">
            <button id="menuLink" onClick={handleHomeClick}>
              Home
            </button>{" "}
            |{" "}
            <button id="menuLink" href="">
              Projects
            </button>{" "}
            |{" "}
            <button id="menuLink" onClick={handleClickAbout}>
              About
            </button>{" "}
            |{" "}
            <a id="menuLink" href="#contact">
              Contact
            </a>
          </div>
        </Fade>
      </div>
      <h1 className="header1">PROJECTS</h1>

      <Fade bottom>
        <div className="cont">
          <h2 className="header2 ">MASTER THESIS</h2>
          <div id="yarntrio">
            <button id="yarntrio" onClick={handleClickMasterThesis}>
              <img alt="Mockup" src={mt1}></img>
              <img alt="Mockup" src={mt2}></img>
              <img alt="Mockup" src={mt3}></img>
            </button>
          </div>
          <p>
            My master thesis objective was to evaluate the difference
            gamification makes in a migraine applicaiton.
            <br />
          </p>
          <div id="more">
            <button id="more" onClick={handleClickMasterThesis}>
              View More ➡️
            </button>
          </div>
        </div>
      </Fade>

      <Fade bottom>
        <div className="cont">
          <h2 className="header2 ">YARN</h2>
          <div id="yarntrio">
            <button id="yarntrio" onClick={handleYarn}>
              <img alt="Mockup" src={yarn1}></img>
              <img alt="Mockup" src={yarn2}></img>
              <img alt="Mockup" src={yarn3}></img>
            </button>
          </div>
          <p>
            Yarn was an app created by me and two of my fellow students, in
            collaboration with marketing students in Perth, Australia. The goal
            was to provide the elderly with a way to cope with loneliness during
            the pandemic. We accomplished this by creating Yarn, an app which
            allowed the elderly to connect to people with the same interests as
            themselves over a video chat. Yarn was designed using the design
            tool Figma and later implemented through Android studios.
          </p>
          <div id="more">
            <button id="more" onClick={handleYarn}>
              View More ➡️
            </button>
          </div>
        </div>
      </Fade>
      <Fade bottom>
        <div className="cont">
          <h2 className="header2">COMBAT COVID</h2>
          <div>
            <button id="virustrio" onClick={handleCombat}>
              <img alt="Mockup" id="virus1" src={virus1}></img>
              <img alt="Mockup" id="virus2" src={virus2}></img>
            </button>
          </div>
          <p>
            Combat Covid on Campus (CCC) was created by me and two other
            students. The app took inspiration from “Popular Times” on Google
            and tackled the question “How to Combat Covid on Campus”. CCC
            allowed users to check-in to campus before going, letting the people
            who wanted to avoid crowds know when campus was busy. The app also
            allowed for potential contract tracing for the COVID-19 virus. CCC
            was designed in Figma and later implemented using React.
          </p>
          <div id="more">
            <button id="more" onClick={handleCombat}>
              View More ➡️
            </button>
          </div>
        </div>
      </Fade>
      <Fade bottom>
        <div className="cont">
          <h2 className="header2">FINDFUN</h2>
          <div id="findfunimg">
            <button id="findfunimg" onClick={handleFind}>
              <img alt="Mockup" src={findfun}></img>
            </button>
          </div>
          <p>
            Findfun is a little side project I created to try out Bored API. I
            created a website where you can set your preferences and Find Fun
            activities for you. The projects is uploaded on GitHub, you can find
            it <a href="https://github.com/Ljungholm-a/findfun"> here</a>
          </p>

          <p></p>
          <div id="more">
            <button id="more" onClick={handleFind}>
              View More ➡️
            </button>
          </div>
        </div>
      </Fade>
      <Fade bottom>
        <div className="cont">
          <h2 className="header2">GREEN LIFE</h2>
          <div id="greenlifeimg">
            <button id="greenlifeimg" onClick={handleGreen}>
              <img alt="Mockup" src={green1}></img>
              <img alt="Mockup" src={green2}></img>
            </button>
          </div>
          <p>
            Green Life is a little side project I created. A prototype for a
            plant shops to show off their plants! Done in Figma.
          </p>

          <p></p>
          <div id="more">
            <button id="more" onClick={handleGreen}>
              View More ➡️
            </button>
          </div>
        </div>

        <div id="backH">
          <button id="BackHome" onClick={handleClick}>
            Back to Home
          </button>
        </div>
      </Fade>
      <div id="divide"></div>
      <div id="contact">
        <Fade>
          <h1 id="bounceH1">contact</h1>
        </Fade>

        <div id="contactcontent">
          <Fade>
            <p>
              <b>Contact via email:</b>
              <br />
              <a href="mailto:aliceljungholm@gmail.com">
                {" "}
                aliceljungholm@gmail.com
              </a>
              <br />
              <br />
              <b>or LinkedIn:</b>
              <br />
              <a
                href="https://www.linkedin.com/in/alice-ljungholm-6b780214b"
                target="_blank"
                rel="noreferrer"
              >
                Alice Ljungholm LinkedIn
              </a>
            </p>
          </Fade>
        </div>
        <div id="piclink">
          <a href="https://www.linkedin.com/in/alice-ljungholm-6b780214b">
            <img alt="" src={linkedin}></img>
          </a>
          <a href="mailto:aliceljungholm@gmail.com">
            <img alt="" src={mailto}></img>
          </a>
        </div>
      </div>
    </div>
  );
};
export default Project;
