import React from "react";
import { useHistory } from "react-router-dom";
import "../StartPage/StartPage";
import hamburg from "../img/hambur.png";
import mailto from "../img/mailto.png";
import linkedin from "../img/linkedin.png";
import Fade from "react-reveal/Fade";

const About = () => {
  const history = useHistory();

  //npm run deploy -- -m "Deploy React app to GitHub Pages"

  window.onscroll = function () {
    scrollFunction();
  };

  function linkClick() {
    let width = window.innerWidth;
    var li = document.getElementById("link");
    var lis = document.getElementById("links");
    if (width < 769) {
      li.style.width = "100%";

      lis.style.display = "block";
    } else {
      if (document.getElementById("link") !== "null") {
        console.log(li.style.width);
        if (li.style.width === "90%") {
          li.style.width = "0.3vw";
          li.style.height = "1vw";
          lis.style.display = "none";
        } else if (li.style.width === "0.3vw") {
          li.style.width = "90%";
          lis.style.display = "block";
        }
      }
    }
  }

  function scrollFunction() {
    let width = window.innerWidth;
    var li = document.getElementById("link");
    var lis = document.getElementById("links");
    if (width < 769) {
      li.style.width = "100%";

      lis.style.display = "block";
    } else {
      if (
        document.body.scrollTop > 50 ||
        document.documentElement.scrollTop > 50
      ) {
        li.style.width = "0.3vw";

        li.style.height = "1vw";
        lis.style.display = "none";
        li.onclick = linkClick;
      } else {
        li.style.width = "90%";
        lis.style.display = "block";
        li.onclick = linkClick;
      }
    }
  }

  function handleClickMasterThesis() {
    history.push("/aliceljungholm/master-thesis");
  }

  function handleClickAbout() {
    history.push("/aliceljungholm/about");
  }
  function handleHomeClick() {
    history.push("/aliceljungholm");
  }

  function handleClickProject() {
    history.push("/aliceljungholm/Project");
  }

  return (
    <div className="main">
      <div className="content">
        <div id="link">
          <img id="linkimg" alt="" source={hamburg}></img>
          <Fade left>
            <div id="links">
              <button id="menuLink" onClick={handleHomeClick}>
                Home
              </button>{" "}
              |{" "}
              <button id="menuLink" onClick={handleClickProject}>
                Projects
              </button>{" "}
              |{" "}
              <button id="menuLink" onClick={handleClickAbout}>
                About
              </button>{" "}
              |{" "}
              <a id="menuLink" href="#contact">
                Contact
              </a>
            </div>
          </Fade>
        </div>

        <div className="aboutheader">
          <div className="headerFade">
            <Fade left>
              <div className="mainH1">
                <h1>
                  <b>about me</b>
                </h1>
              </div>
            </Fade>
          </div>
        </div>
        {/* /**Kanske tar en hoover med information här istället??? */}
      </div>
      <div className="aboutcomp">
        <div className="aboutMe">
          <div id="aboutcontent">
            <Fade left>
              <ul>
                <li>
                  I was born in the late 90's in the northern parts of Sweden
                </li>
                <li>
                  I am a M.Sc. Graduate in Interaction Technology and Design
                  from Umeå University, Sweden{" "}
                </li>
                <li>
                  I am a logical thinker with a lot of creative energy. Best of
                  both worlds I would say
                </li>
                <li>
                  I have experience in multiple coding languagues but prefer
                  Java, Python, HTML, CSS, and JavaScript
                </li>
                <li>Fluent in Figma and Adobe XD</li>
                <li>Familiar with SQL databases, AI, and ML algorithm's</li>
              </ul>
            </Fade>
          </div>
        </div>

        <div id="education"></div>

        <div id="educ">
          <Fade left>
            <h1 id="bounceH1">education</h1>
          </Fade>
          <Fade left>
            <div id="educontent">
              <p>
                Finished my Master's of Engineering in Interaction Technology
                and Design at Umeå Univeristy, Sweden. My education was
                primarily focused on services in mobile systems. Where I became
                an expert in the interaction between man and machine and
                creating technology that works, is durable, and easy and fun to
                use. <br></br>
                The details about my Master Thesis can be found{" "}
                <button id="backHere" onClick={handleClickMasterThesis}>
                  here
                </button>
              </p>
              <p> </p>
            </div>
          </Fade>
        </div>
      </div>

      <div id="skills">
        <Fade left>
          <div id="skillcontent">
            <div id="skill">
              <h3>Skills</h3>
              <li>Clean Code</li>
              <li>Unit Testing</li>
              <li>UX/UI</li>
              <li>Wireframing</li>
              <li>Rapid Prototyping</li>
              <li>User Research</li>
              <li>User Testing</li>
              <li>Mobile/Desktop/Tablet</li>
              <li>iOS and Android</li>
            </div>
            <div id="skill">
              <h3>Tools</h3>
              <ul>
                <li>Figma</li>
                <li>Adobe XD</li>
                <li>JavaScript</li>
                <li>HTML/CSS</li>
                <li>React</li>
                <li>Java</li>
                <li>Node.js</li>
                <li>SQL</li>
                <li>GitHub/GitLab</li>
                <li>Google Analytics </li>
                <li>Miro</li>
              </ul>
            </div>
          </div>
        </Fade>
      </div>
      <div id="divide"></div>
      <div id="contact">
        <Fade>
          <h1 id="bounceH1">contact</h1>
        </Fade>

        <div id="contactcontent">
          <Fade>
            <p>
              <b>Contact via email:</b>
              <br />
              <a href="mailto:aliceljungholm@gmail.com">
                {" "}
                aliceljungholm@gmail.com
              </a>
              <br />
              <br />
              <b>or LinkedIn:</b>
              <br />
              <a
                href="https://www.linkedin.com/in/alice-ljungholm-6b780214b"
                target="_blank"
                rel="noreferrer"
              >
                Alice Ljungholm LinkedIn
              </a>
            </p>
          </Fade>
        </div>
        <div id="piclink">
          <a href="https://www.linkedin.com/in/alice-ljungholm-6b780214b">
            <img alt="" src={linkedin}></img>
          </a>
          <a href="mailto:aliceljungholm@gmail.com">
            <img alt="" src={mailto}></img>
          </a>
        </div>
      </div>
    </div>
  );
};
export default About;
