import { BrowserRouter as Router, Route } from "react-router-dom";
import React from "react";
import "./App.css";
import startPage from "./StartPage/StartPage";
import project from "./Project/Project";
import masterThesis from "./MasterThesis/MasterThesis";
import aboutMe from "./About/About";
import Yarn from "./Project1/Project1";
import Combat from "./Project2/Project2";
import Find from "./Project3/Project3";
import Green from "./Project4/Project4";
import ScrollToTop from "./ScrollToTop";

const App = () => (
  <Router>
    <ScrollToTop />

    <Route path="/aliceljungholm" exact component={startPage} />
    <Route path="/aliceljungholm/Project" exact component={project} />
    <Route
      path="/aliceljungholm/Master-Thesis"
      exact
      component={masterThesis}
    />
    <Route path="/aliceljungholm/About" exact component={aboutMe} />
    <Route path="/aliceljungholm/Project/Yarn" exact component={Yarn} />
    <Route path="/aliceljungholm/Project/Combat" exact component={Combat} />
    <Route path="/aliceljungholm/Project/FindFun" exact component={Find} />
    <Route path="/aliceljungholm/Project/GreenLife" exact component={Green} />
  </Router>
);

export default App;
