import React from "react";
import { useHistory } from "react-router-dom";
import "./MasterThesis.css";

import mt1 from "../img/mock_mt_1.jpeg";
import mt2 from "../img/mock_mt_2.png";
import mt3 from "../img/mock_mt_3.png";
import Fade from "react-reveal/Fade";

const MasterThesis = () => {
  const history = useHistory();

  window.onscroll = function () {
    scrollFunction();
  };
  function linkClick() {
    let width = window.innerWidth;
    var li = document.getElementById("link");
    var lis = document.getElementById("links");
    if (width < 769) {
      li.style.width = "28%";

      lis.style.display = "block";
    } else {
      if (document.getElementById("link") !== "null") {
        console.log(li.style.width);
        if (li.style.width === "90%") {
          li.style.width = "0.3vw";
          li.style.height = "1vw";
          lis.style.display = "none";
        } else if (li.style.width === "0.3vw") {
          li.style.width = "90%";
          lis.style.display = "block";
        }
      }
    }
  }

  function scrollFunction() {
    let width = window.innerWidth;
    var li = document.getElementById("link");
    var lis = document.getElementById("links");
    if (width < 769) {
      li.style.width = "100%";

      lis.style.display = "block";
    } else {
      if (
        document.body.scrollTop > 50 ||
        document.documentElement.scrollTop > 50
      ) {
        li.style.width = "0.3vw";

        li.style.height = "1vw";
        lis.style.display = "none";
        li.onclick = linkClick;
      } else {
        li.style.width = "90%";
        lis.style.display = "block";
        li.onclick = linkClick;
      }
    }
  }

  function handleClickProject() {
    history.push("/aliceljungholm/Project");
  }

  function handleClickAbout() {
    history.push("/aliceljungholm/about");
  }
  function handleHomeClick() {
    history.push("/aliceljungholm");
  }

  return (
    <div className="pro">
      <div id="link">
        <Fade left>
          <div id="links">
            <button id="menuLink" onClick={handleHomeClick}>
              Home
            </button>{" "}
            |{" "}
            <button id="menuLink" onClick={handleClickProject}>
              Projects
            </button>{" "}
            |{" "}
            <button id="menuLink" onClick={handleClickAbout}>
              About
            </button>{" "}
            |{" "}
            <button id="menuLink" href="#contact-me">
              Contact
            </button>
          </div>
        </Fade>
      </div>

      <h1 className="header1">MASTER THESIS</h1>

      <Fade bottom>
        <div className="cont">
          <h2 className="header2 ">MIGRAINE LOGGER</h2>
          <div id="yarntrio">
            <img alt="Mockup" src={mt1}></img>
            <img alt="Mockup" src={mt2}></img>
            <img alt="Mockup" src={mt3}></img>
          </div>

          <div id="thesisText">
            <h3>Objective</h3>
            <p className="leftText">
              The objective of this paper was to investigate how the use of
              gamification in migraine tracking affects the perceived feeling of
              motivation. The measurement of motivation will be done through
              Self Determination Theory.{" "}
            </p>
            <h3>Abstract</h3>
            <p className="leftText">
              {" "}
              This thesis will investigate the possibility of using gamification
              in mobile applications for migraine patients, to see if it will
              help migraine patients with their logging. The problem with
              logging migraines is that patients often forget, or simply choose
              not to log their migraines. To solve this problem, we investigate
              in this thesis whether gamification can make a difference in their
              motivation. To investigate whether gamification makes a
              difference, an AB test was performed. An A prototype, without
              gamification, and a B prototype, without gamification, were
              developed in the initial phases of this thesis, these were tested
              on people outside of the target group. Motivation was measured
              using Self Determination Theory and its sub-theory Organic
              integration. Gamification elements chosen to use in the
              B-prototype is based on the desired feel of the prototype, the
              elements included an avatar, badges, and progress bars. The
              A/B-test was performed on 10 people, 5 for each prototype, and all
              the participants were diagnosed with migraine and had some
              previous experience of logging their migraines and symptoms. The
              result includes interview questions about their experience,
              observations of user tests and answers from a questionnaire. The
              results showed no significant difference between the two
              prototypes; however, discussion and some hypotheses can be created
              by the results. Most difference between the results can be seen in
              integration regulation, a subcategory of motivation, where the
              prototype without gamification got a higher result.
            </p>
          </div>
          <p>A link to the published Thesis will appear here shortly</p>
        </div>
        <div id="backH">
          <button id="BackHome" onClick={handleClickProject}>
            Back to all Projects
          </button>
        </div>
      </Fade>
    </div>
  );
};
export default MasterThesis;
