import React from "react";
import { useHistory } from "react-router-dom";

import yarn1 from "../img/yarn1.png";
import yarn2 from "../img/yarn2.png";
import yarn3 from "../img/yarn3.png";
import lofi1 from "../img/interests.png";
import lofi2 from "../img/conversation.png";
import lofi3 from "../img/ongoing.png";
import yarnstate from "../img/yarnstate.png";
import Fade from "react-reveal/Fade";

const Project1 = () => {
  const history = useHistory();

  window.onscroll = function () {
    scrollFunction();
  };
  function linkClick() {
    let width = window.innerWidth;
    var li = document.getElementById("link");
    var lis = document.getElementById("links");
    if (width < 769) {
      li.style.width = "100%";

      lis.style.display = "block";
    } else {
      if (document.getElementById("link") !== "null") {
        console.log(li.style.width);
        if (li.style.width === "90%") {
          li.style.width = "0.3vw";
          li.style.height = "1vw";
          lis.style.display = "none";
        } else if (li.style.width === "0.3vw") {
          li.style.width = "90%";
          lis.style.display = "block";
        }
      }
    }
  }

  function scrollFunction() {
    let width = window.innerWidth;
    var li = document.getElementById("link");
    var lis = document.getElementById("links");
    if (width < 769) {
      li.style.width = "100%";

      lis.style.display = "block";
    } else {
      if (
        document.body.scrollTop > 50 ||
        document.documentElement.scrollTop > 50
      ) {
        li.style.width = "0.3vw";

        li.style.height = "1vw";
        lis.style.display = "none";
        li.onclick = linkClick;
      } else {
        li.style.width = "90%";
        lis.style.display = "block";
        li.onclick = linkClick;
      }
    }
  }

  function handleClickProject() {
    history.push("/aliceljungholm/Project");
  }

  function handleClickAbout() {
    history.push("/aliceljungholm/about");
  }
  function handleHomeClick() {
    history.push("/aliceljungholm");
  }

  return (
    <div className="pro">
      <div id="link">
        <Fade left>
          <div id="links">
            <button id="menuLink" onClick={handleHomeClick}>
              Home
            </button>{" "}
            |{" "}
            <button id="menuLink" onClick={handleClickProject}>
              Projects
            </button>{" "}
            |{" "}
            <button id="menuLink" onClick={handleClickAbout}>
              About
            </button>{" "}
            |{" "}
            <button id="menuLink" href="#contact-me">
              Contact
            </button>
          </div>
        </Fade>
      </div>

      <h1 className="header1">YARN</h1>

      <Fade bottom>
        <div className="cont">
          <div id="thesisText">
            <div id="sumrole">
              <div>
                <h3>Summary</h3>
                <p className="leftText">
                  Yarn was a project made together with marketing student from
                  Perth, Australia. The goal of the project was to develope
                  something that would work as an aid for elderly during covid.
                  As lack of internet and mobile knowledge can cause the elderly
                  to not keep in touch during the pandemic. This application was
                  made to be easy to use, with few steps.{" "}
                </p>
              </div>
              <div id="rightText">
                <h3>Role</h3>
                <ul>
                  <li>UX-designer</li>
                  <li>Front-End Developer</li>
                </ul>
              </div>
            </div>
            <h3>The design idea</h3>
            <p className="leftText">
              {/*Testing was made on 7 people per iteration, since Normans
              citation. The testing was task-based.*/}{" "}
              The design was made from 3 key points that was given to us by our
              marketing team:
              <ul>
                <li>Inclusive</li>
                <li>Easy to use</li>
                <li>Lead to connectivity</li>
              </ul>
            </p>
            <h3>The arcitecture</h3>
            <p>
              We took the key points we got and mapped out a architecture for
              the application.{" "}
            </p>
            <img alt="architecture" src={yarnstate}></img>
            <p>
              This arcitecture is the main flow of the application, and is, as
              seen in the picture, straight-forward and shallow.
            </p>
            <h3>The Design Stages</h3>
            <h4>LoFi</h4>
            <p>
              The design started on paper, which quickly translated to Figma,
              when we were required to work remotely.{" "}
            </p>
            <div id="yarntrio">
              <img alt="lofi" src={lofi1}></img>
              <img alt="Mockup" src={lofi2}></img>
              <img alt="Mockup" src={lofi3}></img>
            </div>
            <h4>Testing</h4>
            We tested on 5 people per iteration, since Nielsen Norman Group
            recommend that number, and with the time frame, we did not have
            time.
            <h5>Result from testing</h5>
            <p>
              The testing resulted in some few design changes, to clarify
              certain symbols and buttons.
            </p>
          </div>

          <h4>Final Design</h4>
          <div id="yarntrio">
            <img alt="Mockup" src={yarn1}></img>
            <img alt="Mockup" src={yarn2}></img>
            <img alt="Mockup" src={yarn3}></img>
          </div>

          <div id="skills">
            <Fade left>
              <div id="skillcontent">
                <div id="skill">
                  <h3>Skills</h3>
                  <li>Clean Code</li>
                  <li>UX/UI</li>
                  <li>Wireframing</li>
                  <li>Rapid Prototyping</li>
                  <li>User Research</li>
                  <li>User Testing</li>
                  <li>Mobile</li>
                  <li>Android</li>
                </div>
                <div id="skill">
                  <h3>Tools</h3>
                  <ul>
                    <li>Figma</li>
                    <li>Java</li>
                    <li>Android Studios</li>
                    <li>GitHub/GitLab</li>
                  </ul>
                </div>
              </div>
            </Fade>
          </div>
        </div>
        <div id="backH">
          <button id="BackHome" onClick={handleClickProject}>
            Back to all Projects
          </button>
        </div>
      </Fade>
    </div>
  );
};
export default Project1;
